import React, { useState } from 'react'

const Usestate = () => {
    const [suma, setSuma] = useState(10)
    const agregar = () =>{
        setSuma(suma +10)
    }
    const reducir = () =>{
        setSuma(suma -1)
    }
    return (
        <div>
            <h1> Contador es igual a:{suma}</h1>
            <button onClick={agregar}>
                Agregar
            </button>
            <button onClick={reducir}>
                Reducir
            </button>
        </div>
    )
}

export default Usestate
